import React, { useState, useEffect } from 'react';
import LoggedNavBar from './LoggedNavBar';
import * as Bootstrap from 'react-bootstrap';
import '../bootstrap/dist/css/bootstrap.css';
import LoggedRouter from './LoggedRouter';
import { Link } from 'react-router-dom';
import '../App.css';
import SideComponent from './SideComponent';
import axios from 'axios';
import LoadingComponent from './LoadingComponent';

function LoggedHome(props) {
    const [hasSavedNotes, setHasSavedNotes] = useState(false);
    const [isLoading, setIsLoading] = useState(true);
    const [data, setData] = useState('');

    console.log(props.location.username);
    const username = props.location.username;

    useEffect(() => {
        axios.get('/api/getNotes/' + username).then(res => {
            setIsLoading(false);
            if (res.length === 0) {
                setHasSavedNotes(false);
            }

            else {
                setData(res.data);
                console.log(res.data);
            }

        }).catch(err => {
            /*setIsLoading(false);*/
            <Bootstrap.Alert variant="primary">
                <Bootstrap.Alert.Heading>We could't get your password savers</Bootstrap.Alert.Heading>
            </Bootstrap.Alert>
        })
    })

    return (
        <>
            <div>
                <LoggedNavBar>
                    {username}
                </LoggedNavBar>
            </div>
            <div className="Container_page">
                <div className="Left_page bg-dark">
                    <SideComponent>
                        {username}
                    </SideComponent>
                </div>
                <div className="Right_page">
                    <Bootstrap.Nav justify variant="tabs" defaultActiveKey="/home">
                        <Bootstrap.Nav.Item>
                            <Bootstrap.Nav.Link href="#">Notes</Bootstrap.Nav.Link>
                        </Bootstrap.Nav.Item>
                        <Bootstrap.Nav.Item>
                            <Bootstrap.Nav.Link href="#about">About Us</Bootstrap.Nav.Link>
                        </Bootstrap.Nav.Item>
                        <Bootstrap.Nav.Item>
                            <Bootstrap.Nav.Link href="#history">
                                History
                        </Bootstrap.Nav.Link>
                        </Bootstrap.Nav.Item>
                        <Bootstrap.Nav.Item>
                            <Bootstrap.Nav.Link href="#profile">
                                Profile
                    </Bootstrap.Nav.Link>
                        </Bootstrap.Nav.Item>
                    </Bootstrap.Nav>

                    <div>
                        {
                            isLoading === true ?
                                <div className="ml-auto" style={{ marginTop: "150px" }}>
                                    <LoadingComponent />
                                </div>
                                :
                                <div>
                                    <p>Tried to get info</p>





                                </div>
                        }
                    </div>
                </div>



            </div>


        </>
    )
}


export default LoggedHome;



