import React from 'react';
import * as Bootstrap from 'react-bootstrap';
import '../App.css';
import '../bootstrap/dist/css/bootstrap.css';


function LoadingComponent() {

    return (
        <div className="Loading-Page">

            <div className="Flex-container">
                <Bootstrap.Spinner animation="grow" variant="primary" />
                <Bootstrap.Spinner animation="grow" variant="secondary" />
                <Bootstrap.Spinner animation="grow" variant="success" />
                <Bootstrap.Spinner animation="grow" variant="danger" />
            </div>

        </div>
    )
}


export default LoadingComponent;