import React, { createContext, useContext, useState} from 'react';
import {useHistory} from 'react-router-dom';

const userNameContext = createContext();


function UseUserContext() {
    const history = useHistory();
    const username = userNameContext;
    if (!username) {
        throw new Error('No user found');

    }else{
        useContext(username);
    }
}

function UpdateUsername() {
    const[userName, setUserName] = useState('');
    const[loggedIn, setLoggedIn] = useState(false);

    function TypeUser(a) {
        setUserName(a);
    }

    return(
        { userName, setUserName, TypeUser, setLoggedIn, loggedIn}
    )
}


function UserThemeProvider({children}) {
    const UserTheme = UpdateUsername();
    return <userNameContext.Provider value={UserTheme}>{children}</userNameContext.Provider>
}

export {UseUserContext, UserThemeProvider, UpdateUsername};