import React from 'react';
import * as Bootstrap from 'react-bootstrap';
import logo from '../Home_Components/images/lock.png';
import '../App.css';
import '../bootstrap/dist/css/bootstrap.css';
import { UseUserContext, UpdateUsername } from '../UserContext';


function LoggedNavBar(props) {
    console.log(props);
    const signed_User = props.children;
    console.log("This is the sent username", props.children);

    const ownerU = "/api/addNote/" + signed_User;

    const renderTooltip = (props) => (
        <Bootstrap.Tooltip id="button-tooltip" {...props}>
            Create a new password saver
        </Bootstrap.Tooltip>
    );




    return (
        <>
            <Bootstrap.Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
                <Bootstrap.Navbar.Brand href="#home">
                    <img src={logo} alt="" className="Logged-logo" />
                    <span className="ml-1">Alcove App</span></Bootstrap.Navbar.Brand>
                <Bootstrap.Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Bootstrap.Navbar.Collapse id="responsive-navbar-nav">
                    <Bootstrap.Nav className="mr-auto">
                        <Bootstrap.Nav.Link href="#features">Features</Bootstrap.Nav.Link>
                        <Bootstrap.Nav.Link href="#pricing">Pricing</Bootstrap.Nav.Link>
                        <Bootstrap.Nav.Link href="#contactus">Contact Us</Bootstrap.Nav.Link>
                    </Bootstrap.Nav>
                    <Bootstrap.Nav>



                        <Bootstrap.OverlayTrigger
                            placement="right"
                            delay={{ show: 250, hide: 400 }}
                            overlay={renderTooltip}
                        >
                            <div className="mr-3" style={{ cursor: "pointer" }}>
                                <Bootstrap.Dropdown>
                                    <Bootstrap.Dropdown.Toggle id="dropdown-basic">
                                        <svg width="2em" height="2.5em" viewBox="0 0 16 16" className="bi bi-file-earmark-plus" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M4 0h5.5v1H4a1 1 0 0 0-1 1v12a1 1 0 0 0 1 1h8a1 1 0 0 0 1-1V4.5h1V14a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V2a2 2 0 0 1 2-2z" />
                                            <path d="M9.5 3V0L14 4.5h-3A1.5 1.5 0 0 1 9.5 3z" />
                                            <path fillRule="evenodd" d="M8 6.5a.5.5 0 0 1 .5.5v1.5H10a.5.5 0 0 1 0 1H8.5V11a.5.5 0 0 1-1 0V9.5H6a.5.5 0 0 1 0-1h1.5V7a.5.5 0 0 1 .5-.5z" />
                                        </svg>
                                    </Bootstrap.Dropdown.Toggle>
                                    <Bootstrap.Dropdown.Menu>
                                        <Bootstrap.Form className="mx-2 my-2" action={ownerU} method="POST">

                                            <Bootstrap.Form.Group controlId="formBasicText">
                                                <Bootstrap.Form.Label>Website name</Bootstrap.Form.Label>
                                                <Bootstrap.Form.Control type="text" placeholder="Enter webiste name" name="url" />
                                            </Bootstrap.Form.Group>

                                            <Bootstrap.Form.Group controlId="formBasicText">
                                                <Bootstrap.Form.Label>Website username</Bootstrap.Form.Label>
                                                <Bootstrap.Form.Control type="text" placeholder="Enter email" name="username" />
                                            </Bootstrap.Form.Group>

                                            <Bootstrap.Form.Group controlId="formBasicText">
                                                <Bootstrap.Form.Label>Password</Bootstrap.Form.Label>
                                                <Bootstrap.Form.Control type="text" placeholder="Website password" name="password" />
                                            </Bootstrap.Form.Group>
                                            <Bootstrap.Button variant="primary" type="submit">
                                                Submit
                                            </Bootstrap.Button>
                                        </Bootstrap.Form>
                                        
                                    </Bootstrap.Dropdown.Menu>
                                </Bootstrap.Dropdown>



                            </div>
                        </Bootstrap.OverlayTrigger>







                        <Bootstrap.Form inline>
                            <Bootstrap.FormControl type="search" placeholder="Search by IDs" />
                        </Bootstrap.Form>
                        <Bootstrap.Nav.Link eventKey={2} href="#memes">
                            <svg width="2em" height="2em" viewBox="0 0 16 16" className="bi bi-person-circle" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                <path d="M13.468 12.37C12.758 11.226 11.195 10 8 10s-4.757 1.225-5.468 2.37A6.987 6.987 0 0 0 8 15a6.987 6.987 0 0 0 5.468-2.63z" />
                                <path fillRule="evenodd" d="M8 9a3 3 0 1 0 0-6 3 3 0 0 0 0 6z" />
                                <path fillRule="evenodd" d="M8 1a7 7 0 1 0 0 14A7 7 0 0 0 8 1zM0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8z" />
                            </svg>
                        </Bootstrap.Nav.Link>
                    </Bootstrap.Nav>
                </Bootstrap.Navbar.Collapse>
            </Bootstrap.Navbar>


        </>
    )
}

export default LoggedNavBar;