import React from 'react';
import { Switch, Link, BrowserRouter, Route } from 'react-router-dom';
import Home from './Home_Components/Home';
import Nav from './Home_Components/HNavbar';
import { UseUserContext, UserThemeProvider } from './UserContext';
import LoggedHome from './LoggedComponents/LoggedHome';
import Note from './LoggedComponents/LoggedNotes';
import AboutUs from './LoggedComponents/AboutUs';
import History from './LoggedComponents/History';
import UserHome from './LoggedComponents/UserHome';

function HRouter() {
    return (
        <UserThemeProvider>
            <BrowserRouter>
                <Switch>
                    <Route path="/loggedhome" component={LoggedHome} />
                    <Route path="/" component={Home} />
                </Switch>
            </BrowserRouter>
        </UserThemeProvider>
    )
}

export default HRouter;