import React from 'react';
import * as Bootstrap from 'react-bootstrap';
import '../bootstrap/dist/css/bootstrap.css';
import '../App.css';
import logoo from './images/lock.png';

function HNavbar() {
    return (
        <div>
            <Bootstrap.Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" sticky="top">
                <Bootstrap.Navbar.Brand href="#home"><img src={logoo} alt="" />Alcove</Bootstrap.Navbar.Brand>
                <Bootstrap.Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Bootstrap.Navbar.Collapse id="responsive-navbar-nav">
                    <Bootstrap.Nav className="ml-auto">
                        <Bootstrap.Nav.Link>About Us</Bootstrap.Nav.Link>
                        <Bootstrap.Nav.Link>Support Us</Bootstrap.Nav.Link>
                        <Bootstrap.Button variant="outline-primary" className="mr-2 Mobile-omit">Log In</Bootstrap.Button>
                        <Bootstrap.Button variant="outline-primary" className="Mobile-omit">Sign Up</Bootstrap.Button>

                    </Bootstrap.Nav>
                    <Bootstrap.Nav>
                        
                    </Bootstrap.Nav>
                </Bootstrap.Navbar.Collapse>
            </Bootstrap.Navbar>
        </div>
    )
}

export default HNavbar;